.modal {
  justify-content: center;
  align-items: center;
}

.modal-content {
  max-width: 500px;
  animation-name: animatetop;
  animation-duration: .4s;
  box-shadow: 0 4px 8px #0003;
}

@keyframes animatetop {
  from {
    opacity: 0;
    top: -300px;
  }

  to {
    opacity: 1;
    top: 0;
  }
}

.close-button:hover, .close-button:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.auth-tabs {
  justify-content: space-around;
  margin-bottom: 20px;
  display: flex;
}

.auth-tab {
  cursor: pointer;
  background: none;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s;
}

.auth-tab.active {
  border-bottom: 2px solid #333;
  font-weight: bold;
}

.auth-forms {
  flex-direction: column;
  display: flex;
}

.auth-form {
  display: none;
}

.auth-form.active {
  display: block;
}

.auth-form label {
  margin-bottom: 5px;
  display: block;
}

.auth-form input {
  width: 100%;
  border: 1px solid #ccc;
  border-radius: 4px;
  margin-bottom: 15px;
  padding: 10px;
}

.auth-form button {
  width: 100%;
  background-color: var(--background-color);
  color: #201e1e;
  cursor: pointer;
  border: none;
  border-radius: 4px;
  padding: 10px;
  font-size: 16px;
}

.auth-form button:hover {
  background-color: var(--accent-color);
}

.auth-trigger {
  cursor: pointer;
  position: relative;
}

.header-maine__svg {
  fill: var(--button-hover);
}

.header__right {
  align-items: center;
  margin-left: auto;
  margin-right: 0;
  display: flex;
}

.header_dreapta {
  align-items: center;
  margin-top: 12px;
  margin-right: 10px;
  display: flex;
  position: absolute;
  top: 3px;
  right: 3px;
}

.header__registration-icon {
  width: 32px;
  height: 32px;
  filter: brightness(150%) blur(3px);
  animation: glow 1s ease-in-out infinite alternate;
}

@keyframes glow {
  0% {
    filter: brightness(150%) blur(3px);
  }

  100% {
    filter: brightness(200%) blur();
  }
}

.header__registration-icon:hover {
  filter: none;
  animation: none;
}

.social-login {
  justify-content: space-around;
  gap: 15px;
  margin-top: 15px;
  display: flex;
}

.social-btn {
  width: 48%;
  color: #fff;
  cursor: pointer;
  background-color: #4285f4;
  border: none;
  border-radius: 4px;
  padding: 10px;
  font-size: 16px;
}

.social-btn#ios-login, .social-btn#ios-register {
  background-color: var(--background-color);
}

.social-btn:hover {
  opacity: .8;
}

body.dark-mode .modal-content {
  color: #fff;
  background-color: #000;
  border-color: #060606;
}

body.dark-mode .close-button {
  color: #fff;
}

body.dark-mode .auth-tab.active {
  border-bottom: 2px solid #fff;
}

body.dark-mode .auth-form button {
  color: #fff;
  background-color: #444;
}

body.dark-mode .auth-form button:hover {
  background-color: var(--accent-color);
}

body.dark-mode .social-btn {
  background-color: #666;
}

body.dark-mode .social-btn:hover {
  opacity: .9;
}

